import { memo } from 'react';
import { Handle, Position, NodeProps } from 'reactflow';
import {
  Play, Clock, Webhook, Bot, Code, GitBranch, Repeat, Database,
  Zap, User, MessageSquare, Settings, Mail, Calendar, Github,
  FileText, Shield, Brain, Phone, Video, Send, Slack
} from 'lucide-react';

const iconMap: Record<string, any> = {
  Play, Clock, Webhook, Bot, Code, GitBranch, Repeat, Database,
  Zap, User, MessageSquare, Settings, Mail, Calendar, Github,
  FileText, Shield, Brain, Phone, Video, Send, Slack
};

export const TriggerNode = memo(({ data, selected }: NodeProps) => {
  const Icon = iconMap[data.icon] || Play;
  const isStartNode = data.triggerType === 'manual' || data.label === 'Start';

  return (
    <div className={`px-3 py-2 shadow-lg rounded-lg border-2 bg-slate-800 min-w-[160px] ${
      selected ? 'border-blue-500' : 'border-slate-600'
    }`}>
      {!isStartNode && (
        <Handle
          type="target"
          position={Position.Top}
          className="!w-4 !h-4 !bg-blue-500 !border-2 !border-white hover:!scale-150 transition-transform !cursor-crosshair"
          id="target"
          style={{ top: -8, zIndex: 10 }}
        />
      )}

      <div className="flex items-center gap-2">
        <div className="bg-blue-500/20 p-1.5 rounded-lg">
          <Icon className="w-4 h-4 text-blue-400" />
        </div>
        <div className="flex-1">
          <div className="text-xs font-semibold text-white">{data.label}</div>
          <div className="text-[10px] text-slate-400">{data.triggerType || 'Trigger'}</div>
        </div>
      </div>

      <Handle
        type="source"
        position={Position.Bottom}
        className="!w-4 !h-4 !bg-blue-500 !border-2 !border-white hover:!scale-150 transition-transform !cursor-crosshair"
        id="source"
        style={{ bottom: -8, zIndex: 10 }}
      />
    </div>
  );
});

TriggerNode.displayName = 'TriggerNode';

