import React, { useCallback, useRef, useState } from 'react';
import ReactFlow, {
  Background,
  Controls,
  MiniMap,
  Panel,
  Node,
  ReactFlowProvider,
  BackgroundVariant,
  ConnectionLineType,
} from 'reactflow';
import 'reactflow/dist/style.css';

import { useWorkflowStore } from '../../stores/workflowStore';
import { nodeTypes } from './nodes/CustomNodes';
import WorkflowToolbar from './WorkflowToolbar';
import WorkflowCopilot from './WorkflowCopilot';
import {
  Save, Play, Undo, Redo, ZoomIn, ZoomOut, Maximize, Download,
  Settings, ArrowLeft, MoreVertical
} from 'lucide-react';

interface WorkflowBuilderProps {
  workflowId?: string;
  onBack?: () => void;
}

function WorkflowBuilderInner({ workflowId, onBack }: WorkflowBuilderProps) {
  const reactFlowWrapper = useRef<HTMLDivElement>(null);
  const [reactFlowInstance, setReactFlowInstance] = useState<any>(null);
  const [activePanel, setActivePanel] = useState<'copilot' | 'toolbar' | 'editor'>('toolbar');

  const {
    nodes,
    edges,
    onNodesChange,
    onEdgesChange,
    onConnect,
    addNode,
    workflowName,
    setWorkflowName,
  } = useWorkflowStore();

  const onDragOver = useCallback((event: React.DragEvent) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  const onDrop = useCallback(
    (event: React.DragEvent) => {
      event.preventDefault();

      if (!reactFlowWrapper.current || !reactFlowInstance) return;

      const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
      const type = event.dataTransfer.getData('application/reactflow-type');
      const nodeType = event.dataTransfer.getData('application/reactflow-nodetype');
      const label = event.dataTransfer.getData('application/reactflow-label');
      const icon = event.dataTransfer.getData('application/reactflow-icon');

      if (!type) return;

      const position = reactFlowInstance.project({
        x: event.clientX - reactFlowBounds.left,
        y: event.clientY - reactFlowBounds.top,
      });

      const newNode: Node = {
        id: `${type}-${Date.now()}`,
        type: nodeType,
        position,
        data: {
          label: label || type,
          icon: icon || 'Code',
          triggerType: type === 'trigger' ? type : undefined,
          agentType: nodeType === 'agent' ? 'AI Agent' : undefined,
          actionType: nodeType === 'action' ? type : undefined,
        },
      };

      addNode(newNode);
    },
    [reactFlowInstance, addNode]
  );

  const onNodeDragStart = (event: React.DragEvent, item: any) => {
    event.dataTransfer.setData('application/reactflow-type', item.id);
    event.dataTransfer.setData('application/reactflow-nodetype', item.nodeType);
    event.dataTransfer.setData('application/reactflow-label', item.label);
    event.dataTransfer.setData('application/reactflow-icon', item.icon.name || 'Code');
    event.dataTransfer.effectAllowed = 'move';
  };

  const handleSave = () => {
    console.log('Saving workflow...', { nodes, edges, workflowName });
  };

  const handleRun = () => {
    console.log('Running workflow...', { nodes, edges });
  };

  return (
    <div className="flex flex-col h-screen bg-slate-950">
      <div className="bg-slate-900 border-b border-slate-700 px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            {onBack && (
              <button
                onClick={onBack}
                className="p-2 hover:bg-slate-800 rounded-lg transition-colors text-slate-400 hover:text-white"
              >
                <ArrowLeft className="w-5 h-5" />
              </button>
            )}
            <input
              type="text"
              value={workflowName}
              onChange={(e) => setWorkflowName(e.target.value)}
              className="text-xl font-semibold text-white bg-transparent border-none focus:outline-none"
              placeholder="Untitled Workflow"
            />
          </div>

          <div className="flex items-center gap-2">
            <div className="flex items-center gap-1 mr-4">
              <button
                onClick={() => setActivePanel('copilot')}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  activePanel === 'copilot'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-800'
                }`}
              >
                Copilot
              </button>
              <button
                onClick={() => setActivePanel('toolbar')}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  activePanel === 'toolbar'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-800'
                }`}
              >
                Toolbar
              </button>
              <button
                onClick={() => setActivePanel('editor')}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  activePanel === 'editor'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-800'
                }`}
              >
                Editor
              </button>
            </div>

            <button
              onClick={handleSave}
              className="flex items-center gap-2 px-4 py-2 bg-slate-800 hover:bg-slate-700 text-white rounded-lg transition-colors"
            >
              <Save className="w-4 h-4" />
              Save
            </button>
            <button
              onClick={handleRun}
              className="flex items-center gap-2 px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg transition-colors"
            >
              <Play className="w-4 h-4" />
              Run
            </button>
            <button className="p-2 hover:bg-slate-800 rounded-lg transition-colors text-slate-400 hover:text-white">
              <MoreVertical className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>

      <div className="flex-1 flex overflow-hidden">
        <div ref={reactFlowWrapper} className="flex-1 relative">
          <ReactFlow
            nodes={nodes}
            edges={edges}
            onNodesChange={onNodesChange}
            onEdgesChange={onEdgesChange}
            onConnect={onConnect}
            onInit={setReactFlowInstance}
            onDrop={onDrop}
            onDragOver={onDragOver}
            nodeTypes={nodeTypes}
            fitView
            className="bg-slate-950"
            defaultEdgeOptions={{
              animated: true,
              type: 'smoothstep',
              style: { stroke: '#6366f1', strokeWidth: 2 },
            }}
            connectionLineStyle={{ stroke: '#6366f1', strokeWidth: 2 }}
            connectionLineType={ConnectionLineType.SmoothStep}
            snapToGrid={true}
            snapGrid={[15, 15]}
            connectOnClick={true}
            connectionRadius={30}
          >
            <Background color="#334155" variant={BackgroundVariant.Dots} gap={16} size={1} />
            <Controls
              className="bg-slate-800 border border-slate-700 rounded-lg"
              showInteractive={false}
            />
            <MiniMap
              className="bg-slate-800 border border-slate-700 rounded-lg"
              nodeColor={(node) => {
                switch (node.type) {
                  case 'trigger':
                    return '#3b82f6';
                  case 'agent':
                    return '#8b5cf6';
                  case 'condition':
                    return '#f97316';
                  case 'action':
                    return '#06b6d4';
                  case 'loop':
                    return '#60a5fa';
                  case 'knowledge':
                    return '#10b981';
                  case 'human':
                    return '#ec4899';
                  default:
                    return '#64748b';
                }
              }}
            />
            <Panel position="top-center" className="bg-slate-800 border border-slate-700 rounded-lg px-4 py-2">
              <div className="flex items-center gap-2 text-sm text-slate-400">
                <span>{nodes.length} nodes</span>
                <span></span>
                <span>{edges.length} connections</span>
              </div>
            </Panel>
          </ReactFlow>
        </div>

        {activePanel === 'toolbar' && <WorkflowToolbar onNodeDragStart={onNodeDragStart} />}
        {activePanel === 'copilot' && <WorkflowCopilot />}
        {activePanel === 'editor' && (
          <div className="w-96 bg-slate-900 border-l border-slate-700 p-6">
            <h2 className="text-lg font-semibold text-white mb-4">Node Editor</h2>
            <div className="text-sm text-slate-400">
              Click on a node to edit its properties
            </div>
          </div>
        )}
      </div>

      <div className="bg-slate-900 border-t border-slate-700 px-6 py-3">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-2">
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              <span className="text-sm text-slate-400">Ready to deploy</span>
            </div>
          </div>
          <div className="flex items-center gap-2 text-sm text-slate-400">
            <span>Block</span>
            <span></span>
            <span>Status</span>
            <span></span>
            <span>Run ID</span>
            <span></span>
            <span>Duration</span>
            <span></span>
            <span>Timestamp</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default function WorkflowBuilder(props: WorkflowBuilderProps) {
  return (
    <ReactFlowProvider>
      <WorkflowBuilderInner {...props} />
    </ReactFlowProvider>
  );
}
