import React, { useState } from 'react';
import {
  Sparkles, Send, Lightbulb, Wrench, TrendingUp, MessageCircle,
  Code, Play, AlertCircle
} from 'lucide-react';

interface CopilotSuggestion {
  id: string;
  title: string;
  description: string;
  icon: any;
}

const suggestions: CopilotSuggestion[] = [
  {
    id: 'build',
    title: 'Build',
    description: 'Help me build a workflow',
    icon: Code
  },
  {
    id: 'debug',
    title: 'Debug',
    description: 'Help debug my workflow',
    icon: AlertCircle
  },
  {
    id: 'optimize',
    title: 'Optimize',
    description: 'Create a fast workflow',
    icon: TrendingUp
  }
];

export default function WorkflowCopilot() {
  const [messages, setMessages] = useState<Array<{ role: 'user' | 'assistant', content: string }>>([]);
  const [input, setInput] = useState('');
  const [isThinking, setIsThinking] = useState(false);

  const handleSendMessage = async () => {
    if (!input.trim()) return;

    const userMessage = input.trim();
    setInput('');
    setMessages(prev => [...prev, { role: 'user', content: userMessage }]);
    setIsThinking(true);

    setTimeout(() => {
      const assistantMessage = generateResponse(userMessage);
      setMessages(prev => [...prev, { role: 'assistant', content: assistantMessage }]);
      setIsThinking(false);
    }, 1000);
  };

  const generateResponse = (message: string) => {
    const lowerMessage = message.toLowerCase();

    if (lowerMessage.includes('build') || lowerMessage.includes('create')) {
      return "I can help you build a workflow! To get started:\n\n1. Drag a trigger node (like 'Start' or 'Webhook') onto the canvas\n2. Add an Agent block to process the data\n3. Connect them together\n4. Configure each node by clicking on it\n\nWhat kind of workflow would you like to create? For example:\n- Customer support automation\n- Data processing pipeline\n- Email notification system";
    }

    if (lowerMessage.includes('debug') || lowerMessage.includes('error') || lowerMessage.includes('fix')) {
      return "I'll help you debug your workflow! Here are common issues to check:\n\n1. Missing connections between nodes\n2. Unconfigured node parameters\n3. Invalid data transformations\n4. Missing required fields\n\nClick on any node with a red indicator to see specific errors. Would you like me to analyze your workflow for potential issues?";
    }

    if (lowerMessage.includes('optimize') || lowerMessage.includes('improve') || lowerMessage.includes('faster')) {
      return "Let's optimize your workflow! Here are some tips:\n\n1. Use parallel processing where possible\n2. Cache repeated API calls\n3. Add conditions to skip unnecessary steps\n4. Use batch operations for multiple items\n\nWould you like me to suggest specific optimizations for your current workflow?";
    }

    if (lowerMessage.includes('agent')) {
      return "AI Agents are powerful blocks that can:\n\n- Understand natural language\n- Make decisions based on context\n- Use tools to accomplish tasks\n- Generate human-like responses\n\nTo add an agent:\n1. Drag the 'Agent' block from the toolbar\n2. Configure the model (GPT-4, Claude, etc.)\n3. Set the system prompt\n4. Choose available tools\n\nWhat would you like your agent to do?";
    }

    return "I'm here to help you build, debug, and optimize your AI workflows. You can:\n\n- Ask me to build a specific workflow\n- Get help debugging issues\n- Learn about workflow optimization\n- Understand how different blocks work\n\nWhat would you like to know?";
  };

  const handleSuggestionClick = (suggestion: CopilotSuggestion) => {
    setInput(suggestion.description);
  };

  return (
    <div className="w-96 bg-slate-900 border-l border-slate-700 flex flex-col h-full">
      <div className="p-4 border-b border-slate-700">
        <div className="flex items-center gap-2 mb-1">
          <Sparkles className="w-5 h-5 text-violet-400" />
          <h2 className="text-lg font-semibold text-white">Copilot</h2>
        </div>
        <p className="text-sm text-slate-400">Plan, search, build anything</p>
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.length === 0 ? (
          <div className="space-y-4">
            <div className="text-center py-8">
              <div className="bg-violet-500/20 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
                <MessageCircle className="w-8 h-8 text-violet-400" />
              </div>
              <h3 className="text-lg font-semibold text-white mb-2">How can I help?</h3>
              <p className="text-sm text-slate-400">
                Ask me anything about building workflows
              </p>
            </div>

            <div className="space-y-2">
              {suggestions.map((suggestion) => {
                const Icon = suggestion.icon;
                return (
                  <button
                    key={suggestion.id}
                    onClick={() => handleSuggestionClick(suggestion)}
                    className="w-full p-4 bg-slate-800 border border-slate-700 rounded-lg hover:border-violet-500 transition-colors text-left"
                  >
                    <div className="flex items-start gap-3">
                      <div className="bg-violet-500/20 p-2 rounded-lg">
                        <Icon className="w-5 h-5 text-violet-400" />
                      </div>
                      <div>
                        <div className="text-sm font-semibold text-white mb-1">
                          {suggestion.title}
                        </div>
                        <div className="text-xs text-slate-400">
                          {suggestion.description}
                        </div>
                      </div>
                    </div>
                  </button>
                );
              })}
            </div>

            <div className="bg-slate-800/50 border border-slate-700 rounded-lg p-4">
              <div className="flex items-start gap-2 text-xs text-slate-400">
                <Lightbulb className="w-4 h-4 text-yellow-400 flex-shrink-0 mt-0.5" />
                <p>
                  <span className="font-semibold text-white">Tip:</span> Use @ to reference chats, workflows, knowledge, blocks, or templates
                </p>
              </div>
            </div>
          </div>
        ) : (
          <div className="space-y-4">
            {messages.map((message, index) => (
              <div
                key={index}
                className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
              >
                <div
                  className={`max-w-[80%] rounded-lg p-3 ${
                    message.role === 'user'
                      ? 'bg-violet-600 text-white'
                      : 'bg-slate-800 text-slate-200 border border-slate-700'
                  }`}
                >
                  <p className="text-sm whitespace-pre-line">{message.content}</p>
                </div>
              </div>
            ))}
            {isThinking && (
              <div className="flex justify-start">
                <div className="bg-slate-800 border border-slate-700 rounded-lg p-3">
                  <div className="flex items-center gap-2">
                    <div className="flex space-x-1">
                      <div className="w-2 h-2 bg-violet-400 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-violet-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                      <div className="w-2 h-2 bg-violet-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                    </div>
                    <span className="text-sm text-slate-400">Thinking...</span>
                  </div>
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      <div className="p-4 border-t border-slate-700">
        <div className="flex items-center gap-2">
          <div className="flex-1 relative">
            <input
              type="text"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
              placeholder="Ask Copilot anything..."
              className="w-full bg-slate-800 border border-slate-700 rounded-lg pl-4 pr-10 py-3 text-white placeholder-slate-400 focus:outline-none focus:border-violet-500"
            />
            <button className="absolute right-2 top-1/2 transform -translate-y-1/2 text-slate-400 hover:text-white">
              <span className="text-sm">@</span>
            </button>
          </div>
          <button
            onClick={handleSendMessage}
            disabled={!input.trim()}
            className="bg-violet-600 hover:bg-violet-700 disabled:bg-slate-700 disabled:cursor-not-allowed text-white p-3 rounded-lg transition-colors"
          >
            <Send className="w-5 h-5" />
          </button>
        </div>
      </div>
    </div>
  );
}
