import React, { useState, useEffect } from 'react';
import {
  Workflow, Bot, Database, Wrench, Play, Pause, Archive,
  Plus, Search, Filter, Eye, Edit, Trash2, Copy, Download,
  Clock, CheckCircle, AlertCircle, XCircle, Activity,
  Zap, GitBranch, Code, FileText, Settings, ChevronRight, Sparkles
} from 'lucide-react';
import { db } from '../../lib/db';
import { useAuth } from '../../contexts/AuthContext';
import WorkflowBuilder from './WorkflowBuilder';
import WorkflowTemplates from './WorkflowTemplates';

interface WorkflowItem {
  id: string;
  name: string;
  description: string;
  status: string;
  version: number;
  created_at: string;
  updated_at: string;
}

interface AIAgent {
  id: string;
  name: string;
  description: string;
  model: string;
  status: string;
  created_at: string;
}

interface KnowledgeBase {
  id: string;
  name: string;
  description: string;
  document_count: number;
  status: string;
  created_at: string;
}

interface AgentTool {
  id: string;
  name: string;
  description: string;
  tool_type: string;
  is_builtin: boolean;
  is_active: boolean;
}

interface WorkflowExecution {
  id: string;
  workflow_id: string;
  status: string;
  started_at: string;
  completed_at: string | null;
  execution_time_ms: number | null;
}

export default function WorkFlowHub() {
  const { user } = useAuth();
  const [viewMode, setViewMode] = useState<'list' | 'builder' | 'templates'>('list');
  const [selectedWorkflowId, setSelectedWorkflowId] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<'workflows' | 'agents' | 'knowledge' | 'tools' | 'executions'>('workflows');
  const [loading, setLoading] = useState(true);

  const [workflows, setWorkflows] = useState<WorkflowItem[]>([]);
  const [agents, setAgents] = useState<AIAgent[]>([]);
  const [knowledgeBases, setKnowledgeBases] = useState<KnowledgeBase[]>([]);
  const [tools, setTools] = useState<AgentTool[]>([]);
  const [executions, setExecutions] = useState<WorkflowExecution[]>([]);

  const [searchQuery, setSearchQuery] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');

  const [stats, setStats] = useState({
    totalWorkflows: 0,
    activeWorkflows: 0,
    totalAgents: 0,
    totalExecutions: 0,
    successRate: 0
  });

  useEffect(() => {
    if (user) {
      loadAllData();
    }
  }, [user]);

  const loadAllData = async () => {
    setLoading(true);
    await Promise.all([
      loadWorkflows(),
      loadAgents(),
      loadKnowledgeBases(),
      loadTools(),
      loadExecutions(),
      loadStats()
    ]);
    setLoading(false);
  };

  const loadWorkflows = async () => {
    const { data, error } = await db
      .from('workflows')
      .select('*')
      .order('updated_at', { ascending: false });

    if (!error && data) {
      setWorkflows(data);
    }
  };

  const loadAgents = async () => {
    const { data, error } = await db
      .from('ai_agents')
      .select('*')
      .order('created_at', { ascending: false });

    if (!error && data) {
      setAgents(data);
    }
  };

  const loadKnowledgeBases = async () => {
    const { data, error } = await db
      .from('knowledge_bases')
      .select('*')
      .order('created_at', { ascending: false });

    if (!error && data) {
      setKnowledgeBases(data);
    }
  };

  const loadTools = async () => {
    const { data, error } = await db
      .from('agent_tools')
      .select('*')
      .order('name');

    if (!error && data) {
      setTools(data);
    }
  };

  const loadExecutions = async () => {
    const { data, error } = await db
      .from('workflow_executions')
      .select('*')
      .order('started_at', { ascending: false })
      .limit(50);

    if (!error && data) {
      setExecutions(data);
    }
  };

  const loadStats = async () => {
    const { data: workflowData } = await db
      .from('workflows')
      .select('status');

    const { data: agentData } = await db
      .from('ai_agents')
      .select('id');

    const { data: executionData } = await db
      .from('workflow_executions')
      .select('status');

    if (workflowData && agentData && executionData) {
      const activeWorkflows = workflowData.filter((w: any) => w.status === 'active').length;
      const completedExecutions = executionData.filter((e: any) => e.status === 'completed').length;
      const successRate = executionData.length > 0
        ? Math.round((completedExecutions / executionData.length) * 100)
        : 0;

      setStats({
        totalWorkflows: workflowData.length,
        activeWorkflows,
        totalAgents: agentData.length,
        totalExecutions: executionData.length,
        successRate
      });
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active':
      case 'completed':
        return 'bg-green-500/20 text-green-400 border-green-500/30';
      case 'running':
      case 'processing':
        return 'bg-blue-500/20 text-blue-400 border-blue-500/30';
      case 'paused':
      case 'queued':
        return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
      case 'failed':
      case 'error':
        return 'bg-red-500/20 text-red-400 border-red-500/30';
      case 'draft':
      case 'inactive':
        return 'bg-slate-500/20 text-slate-400 border-slate-500/30';
      default:
        return 'bg-slate-500/20 text-slate-400 border-slate-500/30';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'active':
      case 'completed':
        return <CheckCircle className="w-4 h-4" />;
      case 'running':
      case 'processing':
        return <Activity className="w-4 h-4 animate-pulse" />;
      case 'paused':
      case 'queued':
        return <Clock className="w-4 h-4" />;
      case 'failed':
      case 'error':
        return <XCircle className="w-4 h-4" />;
      default:
        return <AlertCircle className="w-4 h-4" />;
    }
  };

  if (viewMode === 'builder') {
    return <WorkflowBuilder workflowId={selectedWorkflowId || undefined} onBack={() => setViewMode('list')} />;
  }

  if (viewMode === 'templates') {
    return (
      <WorkflowTemplates
        onSelectTemplate={(template) => {
          setViewMode('builder');
          setSelectedWorkflowId(null);
        }}
        onBack={() => setViewMode('list')}
      />
    );
  }

  return (
    <div className="flex-1 overflow-auto bg-slate-950">
      <div className="relative h-48 bg-gradient-to-r from-violet-600 to-blue-600 overflow-hidden">
        <div className="absolute inset-0 bg-black/20"></div>
        <div className="relative h-full flex items-center px-8">
          <div className="flex items-center gap-4">
            <div className="bg-white/20 backdrop-blur-sm p-4 rounded-2xl">
              <Workflow className="w-12 h-12 text-white" />
            </div>
            <div className="flex-1">
              <h1 className="text-4xl font-bold text-white mb-2">AI WorkFlow</h1>
              <p className="text-white/90 text-lg">Build and deploy AI agent workflows in minutes</p>
            </div>
            <div className="flex items-center gap-2">
              <button
                onClick={() => setViewMode('templates')}
                className="flex items-center gap-2 px-6 py-3 bg-white/20 hover:bg-white/30 backdrop-blur-sm text-white rounded-xl transition-colors"
              >
                <Sparkles className="w-5 h-5" />
                Browse Templates
              </button>
              <button
                onClick={() => setViewMode('builder')}
                className="flex items-center gap-2 px-6 py-3 bg-white hover:bg-white/90 text-violet-600 rounded-xl font-semibold transition-colors"
              >
                <Plus className="w-5 h-5" />
                Create Workflow
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="p-8">
        <div className="grid grid-cols-1 md:grid-cols-5 gap-6 mb-8">
          <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <p className="text-slate-400 text-sm font-medium mb-1">Total Workflows</p>
                <p className="text-3xl font-bold text-white">{stats.totalWorkflows}</p>
              </div>
              <div className="bg-violet-500/20 p-3 rounded-xl">
                <Workflow className="w-6 h-6 text-violet-400" />
              </div>
            </div>
            <p className="text-slate-400 text-sm">{stats.activeWorkflows} active</p>
          </div>

          <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <p className="text-slate-400 text-sm font-medium mb-1">AI Agents</p>
                <p className="text-3xl font-bold text-white">{stats.totalAgents}</p>
              </div>
              <div className="bg-blue-500/20 p-3 rounded-xl">
                <Bot className="w-6 h-6 text-blue-400" />
              </div>
            </div>
            <p className="text-slate-400 text-sm">Configured agents</p>
          </div>

          <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <p className="text-slate-400 text-sm font-medium mb-1">Executions</p>
                <p className="text-3xl font-bold text-white">{stats.totalExecutions}</p>
              </div>
              <div className="bg-green-500/20 p-3 rounded-xl">
                <Play className="w-6 h-6 text-green-400" />
              </div>
            </div>
            <p className="text-slate-400 text-sm">Total runs</p>
          </div>

          <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <p className="text-slate-400 text-sm font-medium mb-1">Success Rate</p>
                <p className="text-3xl font-bold text-white">{stats.successRate}%</p>
              </div>
              <div className="bg-emerald-500/20 p-3 rounded-xl">
                <CheckCircle className="w-6 h-6 text-emerald-400" />
              </div>
            </div>
            <p className="text-green-400 text-sm">Completion rate</p>
          </div>

          <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <p className="text-slate-400 text-sm font-medium mb-1">Knowledge Bases</p>
                <p className="text-3xl font-bold text-white">{knowledgeBases.length}</p>
              </div>
              <div className="bg-cyan-500/20 p-3 rounded-xl">
                <Database className="w-6 h-6 text-cyan-400" />
              </div>
            </div>
            <p className="text-slate-400 text-sm">For RAG systems</p>
          </div>
        </div>

        <div className="bg-slate-800/50 border border-slate-700 rounded-xl overflow-hidden">
          <div className="border-b border-slate-700">
            <div className="flex items-center gap-1 p-2">
              <button
                onClick={() => setActiveTab('workflows')}
                className={`flex items-center gap-2 px-6 py-3 rounded-lg font-medium transition-colors ${
                  activeTab === 'workflows'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-700/50'
                }`}
              >
                <Workflow className="w-4 h-4" />
                Workflows
              </button>
              <button
                onClick={() => setActiveTab('agents')}
                className={`flex items-center gap-2 px-6 py-3 rounded-lg font-medium transition-colors ${
                  activeTab === 'agents'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-700/50'
                }`}
              >
                <Bot className="w-4 h-4" />
                AI Agents
              </button>
              <button
                onClick={() => setActiveTab('knowledge')}
                className={`flex items-center gap-2 px-6 py-3 rounded-lg font-medium transition-colors ${
                  activeTab === 'knowledge'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-700/50'
                }`}
              >
                <Database className="w-4 h-4" />
                Knowledge Bases
              </button>
              <button
                onClick={() => setActiveTab('tools')}
                className={`flex items-center gap-2 px-6 py-3 rounded-lg font-medium transition-colors ${
                  activeTab === 'tools'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-700/50'
                }`}
              >
                <Wrench className="w-4 h-4" />
                Tools
              </button>
              <button
                onClick={() => setActiveTab('executions')}
                className={`flex items-center gap-2 px-6 py-3 rounded-lg font-medium transition-colors ${
                  activeTab === 'executions'
                    ? 'bg-violet-600 text-white'
                    : 'text-slate-400 hover:text-white hover:bg-slate-700/50'
                }`}
              >
                <Activity className="w-4 h-4" />
                Executions
              </button>
            </div>
          </div>

          <div className="p-6">
            <div className="flex items-center justify-between mb-6">
              <div className="flex items-center gap-4 flex-1">
                <div className="relative flex-1 max-w-md">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-slate-400" />
                  <input
                    type="text"
                    placeholder={`Search ${activeTab}...`}
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="w-full bg-slate-900/50 border border-slate-700 rounded-lg pl-10 pr-4 py-2 text-white placeholder-slate-400 focus:outline-none focus:border-violet-500"
                  />
                </div>
                <select
                  value={statusFilter}
                  onChange={(e) => setStatusFilter(e.target.value)}
                  className="bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-2 text-white focus:outline-none focus:border-violet-500"
                >
                  <option value="all">All Status</option>
                  <option value="active">Active</option>
                  <option value="draft">Draft</option>
                  <option value="paused">Paused</option>
                  <option value="inactive">Inactive</option>
                </select>
              </div>
              <button
                onClick={() => setViewMode('builder')}
                className="bg-violet-600 hover:bg-violet-700 text-white px-6 py-2 rounded-lg flex items-center gap-2 font-medium transition-colors"
              >
                <Plus className="w-5 h-5" />
                Create New
              </button>
            </div>

            {activeTab === 'workflows' && (
              <div className="space-y-4">
                {workflows.length === 0 ? (
                  <div className="text-center py-16">
                    <div className="bg-slate-800/50 w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-4">
                      <Workflow className="w-10 h-10 text-slate-400" />
                    </div>
                    <h3 className="text-xl font-semibold text-white mb-2">No workflows yet</h3>
                    <p className="text-slate-400 mb-6">Create your first AI workflow to get started</p>
                    <button
                      onClick={() => setViewMode('builder')}
                      className="bg-violet-600 hover:bg-violet-700 text-white px-6 py-3 rounded-lg font-medium transition-colors"
                    >
                      Create First Workflow
                    </button>
                  </div>
                ) : (
                  workflows.map((workflow) => (
                    <div
                      key={workflow.id}
                      className="bg-slate-900/50 border border-slate-700 rounded-xl p-6 hover:border-violet-500/50 transition-colors"
                    >
                      <div className="flex items-start justify-between">
                        <div className="flex-1">
                          <div className="flex items-center gap-3 mb-2">
                            <h3 className="text-lg font-semibold text-white">{workflow.name}</h3>
                            <span className={`px-3 py-1 rounded-full text-xs font-medium border flex items-center gap-1 ${getStatusColor(workflow.status)}`}>
                              {getStatusIcon(workflow.status)}
                              {workflow.status}
                            </span>
                            <span className="px-3 py-1 bg-slate-700/50 text-slate-300 rounded-full text-xs font-medium">
                              v{workflow.version}
                            </span>
                          </div>
                          <p className="text-slate-400 mb-4">{workflow.description}</p>
                          <div className="flex items-center gap-4 text-sm text-slate-400">
                            <span className="flex items-center gap-1">
                              <Clock className="w-4 h-4" />
                              Updated {new Date(workflow.updated_at).toLocaleDateString()}
                            </span>
                          </div>
                        </div>
                        <div className="flex items-center gap-2 ml-4">
                          <button className="p-2 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white">
                            <Play className="w-5 h-5" />
                          </button>
                          <button
                            onClick={() => {
                              setSelectedWorkflowId(workflow.id);
                              setViewMode('builder');
                            }}
                            className="p-2 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white"
                          >
                            <Edit className="w-5 h-5" />
                          </button>
                          <button className="p-2 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white">
                            <Copy className="w-5 h-5" />
                          </button>
                          <button className="p-2 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-red-400">
                            <Trash2 className="w-5 h-5" />
                          </button>
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            )}

            {activeTab === 'agents' && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {agents.length === 0 ? (
                  <div className="col-span-full text-center py-16">
                    <div className="bg-slate-800/50 w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-4">
                      <Bot className="w-10 h-10 text-slate-400" />
                    </div>
                    <h3 className="text-xl font-semibold text-white mb-2">No AI agents yet</h3>
                    <p className="text-slate-400 mb-6">Create your first AI agent to automate tasks</p>
                    <button className="bg-violet-600 hover:bg-violet-700 text-white px-6 py-3 rounded-lg font-medium transition-colors">
                      Create First Agent
                    </button>
                  </div>
                ) : (
                  agents.map((agent) => (
                    <div
                      key={agent.id}
                      className="bg-slate-900/50 border border-slate-700 rounded-xl p-6 hover:border-violet-500/50 transition-colors"
                    >
                      <div className="flex items-start justify-between mb-4">
                        <div className="bg-blue-500/20 p-3 rounded-xl">
                          <Bot className="w-6 h-6 text-blue-400" />
                        </div>
                        <span className={`px-3 py-1 rounded-full text-xs font-medium border flex items-center gap-1 ${getStatusColor(agent.status)}`}>
                          {getStatusIcon(agent.status)}
                          {agent.status}
                        </span>
                      </div>
                      <h3 className="text-lg font-semibold text-white mb-2">{agent.name}</h3>
                      <p className="text-slate-400 text-sm mb-4">{agent.description}</p>
                      <div className="flex items-center justify-between text-sm">
                        <span className="text-slate-400">{agent.model}</span>
                        <div className="flex items-center gap-2">
                          <button className="p-1.5 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white">
                            <Edit className="w-4 h-4" />
                          </button>
                          <button className="p-1.5 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white">
                            <Settings className="w-4 h-4" />
                          </button>
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            )}

            {activeTab === 'knowledge' && (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {knowledgeBases.length === 0 ? (
                  <div className="col-span-full text-center py-16">
                    <div className="bg-slate-800/50 w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-4">
                      <Database className="w-10 h-10 text-slate-400" />
                    </div>
                    <h3 className="text-xl font-semibold text-white mb-2">No knowledge bases yet</h3>
                    <p className="text-slate-400 mb-6">Create a knowledge base for document-based AI responses</p>
                    <button className="bg-violet-600 hover:bg-violet-700 text-white px-6 py-3 rounded-lg font-medium transition-colors">
                      Create Knowledge Base
                    </button>
                  </div>
                ) : (
                  knowledgeBases.map((kb) => (
                    <div
                      key={kb.id}
                      className="bg-slate-900/50 border border-slate-700 rounded-xl p-6 hover:border-violet-500/50 transition-colors"
                    >
                      <div className="flex items-start justify-between mb-4">
                        <div className="bg-cyan-500/20 p-3 rounded-xl">
                          <Database className="w-6 h-6 text-cyan-400" />
                        </div>
                        <span className={`px-3 py-1 rounded-full text-xs font-medium border flex items-center gap-1 ${getStatusColor(kb.status)}`}>
                          {getStatusIcon(kb.status)}
                          {kb.status}
                        </span>
                      </div>
                      <h3 className="text-lg font-semibold text-white mb-2">{kb.name}</h3>
                      <p className="text-slate-400 text-sm mb-4">{kb.description}</p>
                      <div className="flex items-center justify-between">
                        <span className="text-slate-400 text-sm">{kb.document_count} documents</span>
                        <div className="flex items-center gap-2">
                          <button className="p-1.5 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white">
                            <FileText className="w-4 h-4" />
                          </button>
                          <button className="p-1.5 hover:bg-slate-700 rounded-lg transition-colors text-slate-400 hover:text-white">
                            <Settings className="w-4 h-4" />
                          </button>
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            )}

            {activeTab === 'tools' && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {tools.map((tool) => (
                  <div
                    key={tool.id}
                    className="bg-slate-900/50 border border-slate-700 rounded-xl p-6 hover:border-violet-500/50 transition-colors"
                  >
                    <div className="flex items-start justify-between mb-4">
                      <div className="bg-orange-500/20 p-3 rounded-xl">
                        <Wrench className="w-6 h-6 text-orange-400" />
                      </div>
                      {tool.is_builtin && (
                        <span className="px-3 py-1 bg-slate-700/50 text-slate-300 rounded-full text-xs font-medium">
                          Built-in
                        </span>
                      )}
                    </div>
                    <h3 className="text-lg font-semibold text-white mb-2">{tool.name}</h3>
                    <p className="text-slate-400 text-sm mb-4">{tool.description}</p>
                    <div className="flex items-center justify-between">
                      <span className="px-3 py-1 bg-slate-700/50 text-slate-300 rounded-full text-xs font-medium">
                        {tool.tool_type}
                      </span>
                      <div className={`w-2 h-2 rounded-full ${tool.is_active ? 'bg-green-400' : 'bg-slate-500'}`}></div>
                    </div>
                  </div>
                ))}
              </div>
            )}

            {activeTab === 'executions' && (
              <div className="space-y-4">
                {executions.length === 0 ? (
                  <div className="text-center py-16">
                    <div className="bg-slate-800/50 w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-4">
                      <Activity className="w-10 h-10 text-slate-400" />
                    </div>
                    <h3 className="text-xl font-semibold text-white mb-2">No executions yet</h3>
                    <p className="text-slate-400">Run a workflow to see execution history</p>
                  </div>
                ) : (
                  executions.map((execution) => (
                    <div
                      key={execution.id}
                      className="bg-slate-900/50 border border-slate-700 rounded-xl p-6 hover:border-violet-500/50 transition-colors"
                    >
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-4">
                          <span className={`px-3 py-1 rounded-full text-xs font-medium border flex items-center gap-1 ${getStatusColor(execution.status)}`}>
                            {getStatusIcon(execution.status)}
                            {execution.status}
                          </span>
                          <span className="text-slate-400 text-sm">
                            Started {new Date(execution.started_at).toLocaleString()}
                          </span>
                          {execution.execution_time_ms && (
                            <span className="text-slate-400 text-sm">
                              {execution.execution_time_ms}ms
                            </span>
                          )}
                        </div>
                        <button className="text-violet-400 hover:text-violet-300 flex items-center gap-1 text-sm font-medium">
                          View Details
                          <ChevronRight className="w-4 h-4" />
                        </button>
                      </div>
                    </div>
                  ))
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
